@import 'sprite/_sprite.scss';

.wrap {
  overflow: hidden;
}

header {
  position: fixed;
  left: 0;
  right: 0;
  padding: 30px 0;
  background-color: #fff;
  border-bottom: 1px solid #ccc;
  z-index: 1;
  .header_area {
    position: relative;
    margin: 0 50px;
    padding-right: 492px;
  }
  .search_area {
    position: relative;
    padding-right: 84px;
  }
  .search_bar {
    width: 100%;
    height: 30px;
    border-radius: 4px;
    border: 1px solid #ccc;
  }
  .btn_submit {
    position: absolute;
    top: 0;
    right: 0;
    width: 70px;
    height: 30px;
    margin-top: 1px;
    border: 0;
    border-radius: 4px;
    background-color: #3498db;
    color: #fff;
    vertical-align: top;
    font-size: 15px;
  }
  .add_btn {
    position: fixed;
    right: 20px;
    bottom: 20px;
    .add_issue {
      font-size: 100px;
      color: #72a498;
    }
  }
}

section {
  margin-top: 93px;
  padding: 20px;
  background-color: #fafafa;
  .title_section {
    height: 40px;
    line-height: 40px;
    background-color: #eddfb8;
    text-align: center;
  }
  .user_info {
    position: absolute;
    top: 50%;
    //@include sprite($mask-circle-white-50x50);
    left: 10px;
    margin-top: -35px;
    width: 50px;
    text-align: center;
    .thumbnail {
      margin-bottom: 2px;
      .ico_user {
        font-size: 50px;
        color: #72a498;
      }
    }
    .nickname {
      font-size: 14px;
    }
  }
  .list_set {
    .list {
      margin-top: 20px;
      padding: 20px;
      background-color: rgb(241, 240, 240);
      &[aria-expanded="false"] {
        .description, .answer_area {
          display: none !important;
        }
      }
    }
    .title_area {
      display: block;
      position: relative;
      overflow: hidden;
      padding-left: 70px;
      color: #000;
      .cont_info {
        overflow: hidden;
        padding: 5px 0 0 20px;
        border-left: 1px solid #ccc;
        font-size: 14px;
        .title {
          display: block;
          margin-bottom: 5px;
          font-size: 20px;
        }
        .category {
          display: block;
        }
        .detail_info {
          overflow: hidden;
          .date {
            float: left;
          }
          dl {
            position: relative;
            float: left;
            margin-left: 12px;
            &:before {
              position: absolute;
              top: 3px;
              bottom: 3px;
              left: -5px;
              width: 1px;
              background-color: #ccc;
              content: '';
            }
            dt, dd {
              float: left;
            }
          }
        }
      }
    }
    .description {
      overflow-y: scroll;
      height: 100px;
      margin-top: 20px;
      padding: 20px;
      border: 1px solid rgba(0,0,0,0.2);
      background-color: #fff;
    }
    .answer_area {
      margin-top: 20px;
      .answer_title {
        overflow: hidden;
        .title {
          font-size: 20px;
          font-weight: bold;
        }
      }
      .add_answer {
        float: right;
        width: 70px;
        height: 24px;
        line-height: 24px;
        text-align: center;
        background-color: #ecc776;
        color: #fff;
        font-size: 14px;
        border: 0;
      }
      .answer_list {
        position: relative;
        margin-top: 20px;
        .description {
          padding-right: 125px;
        }
      }
      .content_area {
        position: relative;
        padding-left: 70px;
      }
      .btn_area {
        position: absolute;
        bottom: 20px;
        right: 20px;
        .btn {
          float: left;
          margin-left: 10px;
          border: 0;
        }
        .recommend {
          @include sprite($icon-love-24x24-white);
          &[aria-selected="true"] {
            @include sprite($icon-love-24x24-red);
          }
        }
      }
    }
  }
}

//SelectBox

.select_area {
  position: absolute;
  top: -3px;
  right: 0;
  .combobox {
    float: left;
    width: 150px;
    margin-left: 14px;
  }
  .Select-control {
    height: 38px;
  }
  .Select-multi-value-wrapper {
    overflow: scroll;
    height: 31px;
  }

  .Select-placeholder {
    margin-top: 2px;
  }
}
